@import (less) "color.less";

@preview-button-width:140px;

.titles{
padding-left: @preview-button-width;
padding-right: @preview-button-width;
}

.titles.padding-prev-page-hide{
  padding-left: 0px ;
  padding-right: 0px;
}

:host(.nav-header-slim) {
  .header-overlay{
  border-bottom: none !important;
} 
  #navigation-header-title {
  height: 2em !important;
  padding-top: .3125em !important;
}
 .titles.padding-prev-page-hide{
  padding-left: 80px ;
  padding-right: 80px;
}
}

// #navigation-header-title.header {
